<template>
  <div>
    <v-alert v-if="error" type="error" v-bind:value="true" style="white-space: pre-wrap;">
      {{ error }}
    </v-alert>   
    <v-select
      v-if="!error && model"
      v-model="selected"
      v-bind:items="model"
      item-text="title"
      item-value="src"
      return-object
      class="d-flex"
      cols="12"
      sm="12"
      style="margin: 0 12px 0px 12px" />
    <mkrcontent v-if="selected?.src" v-bind:src="selected.src" />
  </div>
</template>

<script>

  import schema from '../../schema/select';
  import { appendParams } from '../../helpers/src';
  import MKRContent from '../elements/Content.vue';

  import ajv from 'ajv';
  const ajv_localize = require('ajv-i18n/localize/ru');

  export default {
    name: 'MKRSelector',
    components: {
      mkrcontent: MKRContent
    },
    props: {
      //      
      profile: {
        type: Object,
        required: true
      },
      //      
      getContent: {
        type: Function,
        required: true
      },
      //       Data Lake
      pullData: {
        type: Function,
        required: true
      },
      //          
      path: {
        type: String,
        required: true
      },
      //   
      params: {
        type: Object,
        default: null
      },
      //    
      toPrint: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        //  
        selected: null,
        //   
        refresher: null,
        //  
        model: [],
        //   
        error: null
      };
    },
    watch: {
      profile() {
        //   ,   
        this.onRefresh();
      }
    },
    mounted() {
      //     DOM,   
      this.onRefresh();
    },
    methods: {
      //    
      doRefresh() {
        this.pullData().then((result) =>{
          try {
            //    
            const rules = new ajv({ allErrors: true });
            const validator = rules.compile(schema);
            if (!validator(result)) {
              ajv_localize(validator.errors);
              this.error = JSON.stringify(validator.errors, null, 4);
              return;
            } 
            //    ,  
            this.selected = null;
            this.model = result.map((item) => {
              const result = Object.assign({}, item, {src: appendParams(item.src, item.params)});
              if (item.selected) this.selected = result;
              return result;
            });
            this.error = null;
            this.selected = this.selected || this.model[0];
          } catch (e) {
            this.error = e;
          }
        }).catch((e) => this.error = e);
      },
      //   
      onRefresh() {
        //    ,  
        if (this.refresher) clearTimeout(this.refresher);
        //      Data Lake    50
        this.refresher = setTimeout(this.doRefresh, 50);
      }
    }
  };
</script>

