<template>
  <div class="mkr-tree">
    <tree-item 
      v-bind:expands="expands"
      v-bind:items="tree" />
  </div>
</template>

<script>

  import schema from '../../schema/tree';
  import TreeItem from './TreeItem';

  import ajv from 'ajv';
  const ajv_localize = require('ajv-i18n/localize/ru');

  export default {
    name: 'MKRTree',
    components: {
      TreeItem
    },
    props: {
      //      
      profile: {
        type: Object,
        required: true
      },
      //      
      getContent: {
        type: Function,
        required: true
      },
      //       Data Lake
      pullData: {
        type: Function,
        required: true
      },
      //          
      path: {
        type: String,
        required: true
      },
      //   
      params: {
        type: Object,
        default: null
      },
      //    
      toPrint: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        //  
        tree: [],
        //  
        expands: {},
        //   
        refresher: null,
        //  
        model: [],
        //   
        error: null
      };
    },
    watch: {
      profile() {
        //   ,   
        this.onRefresh();
      }
    },
    mounted() {
      //     DOM,   
      this.onRefresh();
    },
    methods: {
      //  
      makeTreeItems(items) {
        let counter = 0;
        const result = [];
        const expandItem = (expitem) => {
          let node = result;
          expitem.location.split('/').map((title, index, arr) => {
            let item = node.find((element) => element.title === title);
            if (!item) {
              node.push(
                item = {
                  title: title,
                  key: `${title}_${counter++}`,
                  items: []
                }
              );
            }
            if (arr.length - 1 === index) {
              item.link = expitem.link;
            }
            node = item.items;
          });
        };
        items.map((item) => expandItem(item));
        return result;
      },
      //    
      doRefresh() {
        this.pullData().then((items) =>{
          try {
            //    
            const rules = new ajv({ allErrors: true });
            const validator = rules.compile(schema);
            if (!validator(items)) {
              ajv_localize(validator.errors);
              this.error = JSON.stringify(validator.errors, null, 4);
              return;
            } 
            //    ,  
            this.error = null;
            this.expands = {};
            this.tree = this.makeTreeItems(items);
          } catch (e) {
            this.error = e;
          }
        }).catch((e) => this.error = e);
      },
      //   
      onRefresh() {
        //    ,  
        if (this.refresher) clearTimeout(this.refresher);
        //      Data Lake    50
        this.refresher = setTimeout(this.doRefresh, 50);
      }
    }
  };
</script>

<style scoped>
  .mkr-tree {
    overflow-y: hidden;
    overflow-x: auto;
  }
</style>
