<template>
  <div>
    <v-alert v-if="error" type="error" v-bind:value="true" style="white-space: pre-wrap;">
      {{ error }}
    </v-alert>    
    <dhsection v-if="!error && model" v-bind:section="model" />
  </div>
</template>

<script>

  import DHSection from './Section.vue';
  import schema from '../../schema/grid';
  import ajv from 'ajv';
  const ajv_localize = require('ajv-i18n/localize/ru');

  export default {
    name: 'MKRGrid',
    components: {
      dhsection: DHSection
    },
    props: {
      //      
      profile: {
        type: Object,
        required: true
      },
      //      
      getContent: {
        type: Function,
        required: true
      },
      //       Data Lake
      pullData: {
        type: Function,
        required: true
      },
      //          
      path: {
        type: String,
        required: true
      },
      //   
      params: {
        type: Object,
        default: null
      },
      //    
      toPrint: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        //   
        refresher: null,
        //  
        model: {},
        //   
        error: null
      };
    },
    watch: {
      profile() {
        //   ,   
        this.onRefresh();
      }
    },
    mounted() {
      //     DOM,   
      this.onRefresh();
    },
    methods: {
      //    
      doRefresh() {
        this.pullData().then((result) =>{
          try {
            //    
            const rules = new ajv({ allErrors: true });
            const validator = rules.compile(schema);
            if (!validator(result)) {
              ajv_localize(validator.errors);
              this.error = JSON.stringify(validator.errors, null, 4);
              return;
            } 
            //    ,  
            this.model = result;
            this.error = null;
          } catch (e) {
            this.error = e;
          }
        }).catch((e) => this.error = e);
      },
      //   
      onRefresh() {
        //    ,  
        if (this.refresher) clearTimeout(this.refresher);
        //      Data Lake    50
        this.refresher = setTimeout(this.doRefresh, 50);
      }
    }
  };
</script>

